"use client";

import React, { useState, useEffect, useRef } from 'react';
import { User, Calendar, Clock, Phone, MapPin, Save, Search, X, Loader2, ArrowLeft } from 'lucide-react';
import Header from '../components/'; // ← ඔයාගේ Header path එක මේකට ගලපන්න
import { useAuth } from '../context/AuthContext';

// ────── LOCATIONIQ API KEY ──────
const LOCATIONIQ_API_KEY = "pk.83ce678095a5989ba69f8649e97e1135";
// ─────────────────────────────────

export default function ProfilePage() {
  const { user, updateUserProfile } = useAuth(); // AuthContext එකෙන් user එක ගන්නවා + update function

  const [formData, setFormData] = useState({
    gender: '',
    birthTime: '',
    dateOfBirth: '',
    whatsappNumber: '',
    birthLocation: '',
    latitude: '',
    longitude: ''
  });

  const [query, setQuery] = useState('');
  const [suggestions, setSuggestions] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  const [showDropdown, setShowDropdown] = useState(false);
  const [saving, setSaving] = useState(false);

  const debounceRef = useRef<NodeJS.Timeout | undefined>(undefined);

  // Load saved profile data when page loads
  useEffect(() => {
    if (user?.profile) {
      setFormData({
        gender: user.profile.gender || '',
        birthTime: user.profile.birthTime || '',
        dateOfBirth: user.profile.dateOfBirth || '',
        whatsappNumber: user.profile.whatsappNumber || '',
        birthLocation: user.profile.birthLocation || '',
        latitude: user.profile.latitude || '',
        longitude: user.profile.longitude || ''
      });

      // If birthLocation already set, show it in the search box temporarily
      if (user.profile.birthLocation) {
        setQuery(user.profile.birthLocation);
      }
    }
  }, [user]);

  const searchLocationIQ = async (q: string) => {
    if (q.trim().length < 2) {
      setSuggestions([]);
      return;
    }

    setLoading(true);
    try {
      const res = await fetch(
        `https://api.locationiq.com/v1/autocomplete?key=${LOCATIONIQ_API_KEY}&q=${encodeURIComponent(q)}&limit=8&countrycodes=lk&accept-language=en,si,ta`
      );
      const data = await res.json();

      if (Array.isArray(data)) {
        setSuggestions(data);
        setShowDropdown(true);
      }
    } catch (err) {
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    if (debounceRef.current) clearTimeout(debounceRef.current);
    debounceRef.current = setTimeout(() => searchLocationIQ(query), 400);
  }, [query]);

  const selectPlace = (place: any) => {
    const displayName = place.display_name || place.display_place || place.address?.city || place.name;

    setFormData(prev => ({
      ...prev,
      birthLocation: displayName,
      latitude: parseFloat(place.lat).toFixed(6),
      longitude: parseFloat(place.lon).toFixed(6)
    }));

    setQuery(displayName);
    setSuggestions([]);
    setShowDropdown(false);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async () => {
    setSaving(true);
    try {
      // Save to AuthContext (or backend API)
      await updateUserProfile({
        gender: formData.gender,
        birthTime: formData.birthTime,
        dateOfBirth: formData.dateOfBirth,
        whatsappNumber: formData.whatsappNumber,
        birthLocation: formData.birthLocation,
        latitude: formData.latitude,
        longitude: formData.longitude
      });

      alert('Profile updated successfully!');
    } catch (err) {
      alert('Failed to save profile. Try again.');
    } finally {
      setSaving(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Header */}
      <Header />

      {/* Back Button (mobile friendly) */}
      <div className="bg-white border-b border-gray-200 px-4 py-3 md:hidden flex items-center gap-3">
        <button onClick={() => window.history.back()} className="p-2 hover:bg-gray-100 rounded-lg">
          <ArrowLeft className="w-5 h-5" />
        </button>
        <span className="font-semibold">Edit Profile</span>
      </div>

      {/* Main Content */}
      <div className="max-w-2xl mx-auto p-6 pt-10">
        <div className="text-center mb-10 hidden md:block">
          <div className="inline-flex items-center justify-center w-20 h-20 bg-blue-600 rounded-full mb-4">
            <User className="w-10 h-10 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-black mb-2">Edit Profile</h1>
          <p className="text-gray-600">Update your personal information</p>
        </div>

        <div className="bg-white rounded-2xl shadow-lg p-8 space-y-6">

          {/* Gender */}
          <div>
            <label className="block text-black font-semibold mb-2">Gender</label>
            <select
              name="gender"
              value={formData.gender}
              onChange={handleInputChange}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            >
              <option value="">Select Gender</option>
              <option value="male">Male</option>
              <option value="female">Female</option>
              <option value="other">Other</option>
            </select>
          </div>

          {/* Date of Birth */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <Calendar className="w-5 h-5 text-blue-600" />
              Date of Birth
            </label>
            <input
              type="date"
              name="dateOfBirth"
              value={formData.dateOfBirth}
              onChange={handleInputChange}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            />
          </div>

          {/* Birth Time */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <Clock className="w-5 h-5 text-blue-600" />
              Birth Time
            </label>
            <input
              type="time"
              name="birthTime"
              value={formData.birthTime}
              onChange={handleInputChange}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            />
          </div>

          {/* WhatsApp Number */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <Phone className="w-5 h-5 text-blue-600" />
              WhatsApp Number
            </label>
            <input
              type="tel"
              name="whatsappNumber"
              value={formData.whatsappNumber}
              onChange={handleInputChange}
              placeholder="+94 77 123 4567"
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            />
          </div>

          {/* Birth Location */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <MapPin className="w-5 h-5 text-blue-600" /> Birth Location (Sri Lanka)
            </label>

            <div className="relative">
              <div className="relative">
                <input
                  type="text"
                  value={query}
                  onChange={(e) => setQuery(e.target.value)}
                  onFocus={() => suggestions.length > 0 && setShowDropdown(true)}
                  placeholder="කොළඹ, මාතර, යාපනය, Galle..."
                  className="w-full pl-11 pr-10 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
                />
                <Search className="absolute left-3 top-3.5 w-5 h-5 text-gray-400" />
                {query && (
                  <button onClick={() => { setQuery(''); setSuggestions([]); setFormData(prev => ({ ...prev, birthLocation: '', latitude: '', longitude: '' })); }}
                    className="absolute right-3 top-3.5 text-gray-400 hover:text-gray-700">
                    <X className="w-5 h-5" />
                  </button>
                )}
              </div>

              {showDropdown && (
                <div className="absolute top-full mt-2 w-full bg-white border-2 border-gray-200 rounded-lg shadow-2xl z-50 max-h-96 overflow-y-auto">
                  {loading ? (
                    <div className="p-4 flex items-center gap-3 text-gray-500">
                      <Loader2 className="w-5 h-5 animate-spin" />
                      Searching...
                    </div>
                  ) : (
                    suggestions.map((place, i) => (
                      <button
                        key={i}
                        onClick={() => selectPlace(place)}
                        className="w-full text-left px-4 py-3 hover:bg-blue-50 border-b border-gray-100 last:border-b-0 flex items-start gap-3"
                      >
                        <MapPin className="w-5 h-5 text-blue-600 mt-0.5 flex-shrink-0" />
                        <div>
                          <div className="font-medium text-black">
                            {place.display_place || place.address?.city || place.address?.town || place.name}
                          </div>
                          <div className="text-sm text-gray-600">
                            {place.display_name?.split(',').slice(1).join(',') || 'Sri Lanka'}
                          </div>
                        </div>
                      </button>
                    ))
                  )}
                </div>
              )}
            </div>

            {/* Selected Location */}
            {formData.birthLocation && (
              <div className="mt-4 p-5 bg-blue-50 border-2 border-blue-200 rounded-lg">
                <p className="font-bold text-black mb-2">Selected Birth Place</p>
                <p className="text-lg text-gray-800">{formData.birthLocation}</p>
                <div className="grid grid-cols-2 gap-4 mt-4 text-sm">
                  <div className="bg-white p-3 rounded">
                    <span className="text-gray-600">Latitude</span>
                    <code className="block font-mono text-blue-700">{formData.latitude}</code>
                  </div>
                  <div className="bg-white p-3 rounded">
                    <span className="text-gray-600">Longitude</span>
                    <code className="block font-mono text-blue-700">{formData.longitude}</code>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Submit Button */}
          <button
            onClick={handleSubmit}
            disabled={saving}
            className="w-full bg-blue-600 text-white font-bold py-4 rounded-lg hover:bg-blue-700 transition-all flex items-center justify-center gap-2 shadow-lg text-lg disabled:opacity-70"
          >
            {saving ? (
              <>
                <Loader2 className="w-6 h-6 animate-spin" /> Saving...
              </>
            ) : (
              <>
                <Save className="w-6 h-6" /> Update Profile
              </>
            )}
          </button>
        </div>
      </div>
    </div>
  );
}